.board {
  width: 100vw;
  height: 100vh;
  background-color: rgb(10, 51, 10);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px;
  box-sizing: border-box;
  overflow: hidden;
}

.start {
  width: 100vw;
  height: 100vh;
  background-color: rgb(10, 51, 10);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nametext {
  color: white;
  margin: 10px;
  font-size: 20px;
}

.button {
  font-size: 18px;
  border-radius: 5px;
}

.input, .button.name {
  margin: 30px;
}

.button.name, button.put {
  background-color: white;
  color: rgb(10, 51, 10);
}

.button.ok {
  background-color: rgb(10, 51, 10);
  color: white;
}

.name {
  color: white;
  font-size: 20px;
  text-align: center;
}

.playerSpace {
  height: 40%;
}

.tableSpace {
  height: 20%;
  display: flex;
  align-items: center;
}

.playerLine {
  height: 45%;
}

.tableLine {
  height: 90%;
  flex: 1;
}

.cardList {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.cardList.low {
  height: 80%;
}

.cardList.low .card {
  margin-top: 4%;
}

.card {
  height: 80%;
}

.card.shown, .card.blind {
  margin: 0px 10px 0px 10px;
}

.card.pile {
  margin-right: -40%;
}

.blindAndShown {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.card.selected {
  margin-bottom: 20px;
}

.container, .row, .col-6 {
  height: 100%;
}

textarea {
  width: 140%;
}

.deck {
  margin-right: 30px;
}

.card.disavailable:hover {
  cursor: not-allowed
}

.card.available:hover {
  cursor: pointer
}

.container {
  max-width: 100%;
}

